import React, { useState } from "react";
import firebase from "gatsby-plugin-firebase"
import ReactMarkdown from "react-markdown";
import MDEditor from '@uiw/react-md-editor';

import SortableTree, { addNodeUnderParent, removeNodeAtPath, getNodeAtPath, getNodeKey } from 'react-sortable-tree'; //, { addNodeUnderParent, getNodeKey }
import 'react-sortable-tree/style.css'; // This only needs to be imported once in your app

import UniformsSubmitObjectUp from '../components/UniformsSubmitObjectUp';
import { userCraft } from "./login"



import Header from '../components/header';

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'


const isBrowser = typeof window !== `undefined`;

export default () => {
    const [selectedMarkdown, setSelectedMarkdown] = useState('-MD9RyMs4boB6hZNXLa0');

    if (!isBrowser) return null


    return <>
        <Header siteTitle={"Econolabs"} />

        <Row>
            <Col sm="6">
                <TableOfContents setSelectedMarkdown={setSelectedMarkdown} />
            </Col>
            <Col sm="6" >
                <div style={{height: '80vh', borderColor: 'red'}}>
                <ShowParagraph selectedMarkdown={selectedMarkdown} />
                </div>
               
          
            </Col>
        </Row>

    </>
}

class TableOfContents extends React.Component {

    constructor(props) {
        super(props);
        this.updateTreeData = this.updateTreeData.bind(this);
        this.addNode = this.addNode.bind(this);
        this.removeNode = this.removeNode.bind(this);
        this.submitUniform = this.submitUniform.bind(this);
        this.state = {
            treeData: [{ title: '' }],
            showConfirmationModal: false,
            rowInfo: null,
            newnode: null,
            login: null
        };
    }

    componentDidMount() {
        //      console.log('component Did Mount');
        userCraft.getItem('currentPublication')
            .then(res => this.setState({ treeData: res }));
        userCraft.getItem('login')
            .then(res => this.setState({ login: res }));;
    }

    addNode(rowInfo, title) {
        console.log(rowInfo);
        let NEW_NODE = { title: this.state.newnode.title, url: this.state.newnode.url };
        let { node, treeIndex, path } = rowInfo;
        path.pop();
        let parentNode = getNodeAtPath({
            treeData: this.state.treeData,
            path: path,
            getNodeKey: ({ treeIndex }) => treeIndex,
            ignoreCollapsed: true
        });
        let getNodeKey = ({ node: object, treeIndex: number }) => {
            return number;
        };
        let parentKey = getNodeKey(parentNode);
        if (parentKey === -1) {
            parentKey = null;
        }
        let newTree = addNodeUnderParent({
            treeData: this.state.treeData,
            newNode: NEW_NODE,
            expandParent: true,
            parentKey: parentKey,
            getNodeKey: ({ treeIndex }) => treeIndex
        });
        this.setState({ treeData: newTree.treeData, showConfirmationModal: false, newnode: null }, () => this.updateTreeData(newTree.treeData));
    }

    removeNode(rowInfo) {
        console.log(rowInfo);
        let { node, treeIndex, path } = rowInfo;
        this.setState({
            treeData: removeNodeAtPath({
                treeData: this.state.treeData,
                path: path,   // You can use path from here
                getNodeKey: ({ node: TreeNode, treeIndex: number }) => {
                    // console.log(number);
                    return number;
                },
                ignoreCollapsed: false,
            })
        }, () => this.updateTreeData(this.state.treeData))
    }

    updateTreeData(treeData) {
        userCraft.setItem('currentPublication', treeData)
            .then(res => this.setState({ treeData }));
    }

    submitUniform(formObjectRes) {
        let newPostKey = firebase.database().ref("/media").push().key;
        let { content, ...post } = formObjectRes;
        let newnode = { ...post, url: newPostKey };
        let fireMediaPost = {
            content: { content: formObjectRes.content },
            title: formObjectRes.title,
            comment: formObjectRes.comment,
            user: this.state.login,
            url: newPostKey,
            type: 'markdown'
        };
        console.log(newnode);
        // console.log(fireMediaPost);
        let updates = {};
        updates['/media/' + newPostKey] = fireMediaPost;
        this.setState({ newnode }, () => firebase.database().ref().update(updates).then(() => console.log('Saved')));
    }

    render() {
        let slideObject = {
            title: ' ',
            type: 'object',
            properties: {
                title: { type: 'string', label: '' },
                content: { type: 'string', label: '' },
                comment: {
                    type: 'string', label: ""
                    // uniforms: { component: 'LongTextField' }
                }
            },
            required: ['title', 'content', 'comment']
        }
        return (<>
            <div className="m-2" style={{ height: '80vh' }}>
                <SortableTree
                    treeData={this.state.treeData}
                    onChange={this.updateTreeData}
                    generateNodeProps={rowInfo => ({
                        buttons: [
                            <div >
                                <Button size="sm" variant="outline-danger" className="mr-1" onClick={(event) => this.removeNode(rowInfo)}>X</Button>
                                <Button size="sm" variant="outline-secondary" onClick={() => this.setState({ showConfirmationModal: true, rowInfo: rowInfo })} >+_</Button>  {/*{(event) => this.addNode(rowInfo)} */}
                                <Button size="sm" variant="outline-primary" onClick={() => this.props.setSelectedMarkdown(rowInfo?.node?.url || '-MD9RyMs4boB6hZNXLa0')}>i</Button>
                            </div>,
                        ]
                        // ,
                        // style: {
                        //     height: '50px',
                        // }
                    })}


                />
            </div>
            <Modal show={this.state.showConfirmationModal} onHide={() => this.setState({ showConfirmationModal: false })} centered dialogClassName={`modal-xl`}>
                <Modal.Body>
                    <UniformsSubmitObjectUp
                        uniformSchema={slideObject}
                        submitUniform={this.submitUniform}
                        model={{}}
                    />
                </Modal.Body>

                <Modal.Footer>
                    <Button size="sm" variant="outline-secondary" onClick={() => this.setState({ showConfirmationModal: false })}>Cancel</Button>
                    {!!this.state.newnode ?
                        <Button size="sm" variant="outline-secondary" disabled={!this.state.newnode} onClick={(event) => this.addNode(this.state.rowInfo, this.state.newnode.title)}></Button> :
                        <Button size="sm" variant="outline-secondary" disabled></Button>
                    }

                </Modal.Footer>
            </Modal>
        </>
        );
    }
}



function ShowParagraph(props) {
    const [data, setData] = React.useState(null);
    const [value, setValue] = React.useState("**Hello world!!!**");

    React.useEffect(() => {
        firebase
            .database()
            .ref("/media")
            .child(`${props.selectedMarkdown}`)
            .once("value")
            .then(snapshot => {
                setData(snapshot.val());
                setValue(snapshot.val().content?.content)
            })
    }, [props.selectedMarkdown])

    console.log(data);

    function doSave() {
        let oldPostKey = data.url;
        let fireMediaPost = { ...data, content: { content: value } };
        // console.log(fireMediaPost);
        let updates = {};
        updates['/media/' + oldPostKey] = fireMediaPost;
        firebase.database().ref().update(updates).then(() => console.log('Saved'));
    }



    return <>
        <Button size="sm" variant="outline-secondary" onClick={() => doSave()} >Save</Button>  {/*{(event) => this.addNode(rowInfo)} */}
        {!!data?.title ? <b><ReactMarkdown source={data.title} escapeHtml={false} /></b> : null}
        {!!data?.comment ? <small><ReactMarkdown source={data.comment} escapeHtml={false} /></small> : null}
        {/* {!!data?.content?.content ? <ReactMarkdown source={data.content.content} escapeHtml={false} /> : "..."} */}

        <MDEditor
            value={value}
            onChange={setValue}
        />
        {/* <MDEditor.Markdown source={value} /> */}
    </>
}

