function OneColumnUniform(props) {
    const [userCraftLogin, setUserCraftLogin] = useState(null);

    useEffect(() => {
        async function getUserData() {
            //     // You can await here
            let userCraftLogin = await userCraft.getItem('login');
            setUserCraftLogin(userCraftLogin);
        }
        getUserData();
    }, []);

    function doCreateNewMediaBlock(input) {
        console.log(input);
        // let newPostKey = firebase.database().child('media').push().key;
        // console.log(newPostKey);
        // newPostKey.set({ ...input, author: userCraftLogin, date: Date.now(), key: newPostKey }).then(() => {
        //     console.log('Saved!!!')
            props.submitUniform(input);
        // })
        //     .catch(function (err) {
        //         // This code runs if there were any errors
        //         console.log(err);
        //     });
    }


    let schema = { ...props.uniformSchema };
    let uniformWithValidator = new JSONSchemaBridge(schema, createValidator(schema));

    return <div
        style={{
            margin: `0 auto`,
            maxWidth: 800,
            padding: '2rem'
        }}>
        <div className="m-3">
            <AutoForm
                schema={uniformWithValidator}
                model={props.model}
                onSubmit={input => doCreateNewMediaBlock(input)}
            >
                {Object.keys(props.uniformSchema.properties).map(propertyKey =>
                    <div key={propertyKey}>
                        {propertyKey.includes('content') || propertyKey.includes('comment') ?
                            <LongTextField name={propertyKey} />
                            : <AutoField name={propertyKey} />}

                    </div>
                )}
                <SubmitField />
            </AutoForm>

        </div>
    </div>
}